import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  switchMap,
  catchError,
} from 'rxjs/operators';
import { MoviesService } from '../../services/posts.service';

@Component({
  selector: 'app-typeahead-search',
  templateUrl: './typeahead-search.component.html',
  styles: [
    `
      .form-control {
        width: 300px;
      }
    `,
  ],
})
export class TypeaheadSearchComponent {
  public model: any;
  public tv_details: any;
  public media_id: any;
  constructor(private movieService: MoviesService) {}

  search = (text$: Observable<string>) => {
    return text$.pipe(
      debounceTime(200),
      distinctUntilChanged(),
      switchMap((searchText: string) =>
        this.movieService.getSearchResults(searchText)
      )
    );
  };

  addMedia1() {
    this.media_id = document.getElementById('id')?.innerHTML;
    var name = document.getElementById('name')?.innerHTML;
    var poster_path: any = document.getElementById('poster')?.innerHTML;

    localStorage.setItem(
      this.media_id,
      this.media_id + '@' + name + poster_path + '$myMovie'
    );
  }

  addMedia2() {
    this.media_id = document.getElementById('id')?.innerHTML;
    var name = document.getElementById('name')?.innerHTML;
    var poster_path: any = document.getElementById('poster')?.innerHTML;

    localStorage.setItem(
      this.media_id,
      this.media_id + '@' + name + poster_path + '$myTv'
    );
  }

  inputFormatMediaValue(value: any) {
    if (value.title) return value.title;
    return value.name;
  }
}
