import { Component, OnInit } from '@angular/core';
import { MoviesService } from '../../services/posts.service';

@Component({
  selector: 'app-carousel-type1',
  templateUrl: './carousel-type1.component.html',
  styleUrls: ['./carousel-type1.component.css'],
})
export class CarouselType1Component implements OnInit {
  public current: any;
  constructor(private movieService: MoviesService) {}

  ngOnInit(): void {
    this.fetchData();
  }

  fetchData() {
    this.movieService.getCurrentPlaying().subscribe((res) => {
      this.current = res;
    });
  }
}
