import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MoviesService } from '../../services/posts.service';

@Component({
  selector: 'app-carousel-type3',
  templateUrl: './carousel-type3.component.html',
  styleUrls: ['./carousel-type3.component.css'],
})
export class CarouselType3Component implements OnInit {
  public id: any;
  public mobile: any;
  public mvrecom: any;
  public mvrecomFormatted: any;
  public mvsimilar: any;
  public mvsimilarFormatted: any;
  public tvrecom: any;
  public tvrecomFormatted: any;
  public tvsimilar: any;
  public tvsimilarFormatted: any;
  public recomShow: any;
  public similarShow: any;
  constructor(
    private route: ActivatedRoute,
    private movieService: MoviesService
  ) {}

  ngOnInit(): void {
    this.id = this.route.snapshot.paramMap.get('id');
    if (window.screen.width === 360) {
      this.mobile = true;
    }
    this.fetchData();
  }

  fetchData() {
    this.movieService.getRecomMovies(this.id).subscribe((res) => {
      this.mvrecom = res;
      this.mvrecomFormatted = [];
      var j = -1;

      for (var i = 0; i < this.mvrecom.length; i++) {
        if (i % 6 == 0) {
          j++;
          this.mvrecomFormatted[j] = [];
          this.mvrecomFormatted[j].push(this.mvrecom[i]);
          this.recomShow = true;
        } else {
          this.mvrecomFormatted[j].push(this.mvrecom[i]);
          this.recomShow = true;
        }
      }
      console.log(this.mvrecom.length);
    });

    this.movieService.getSimilarMovies(this.id).subscribe((res) => {
      this.mvsimilar = res;
      this.mvsimilarFormatted = [];
      var j = -1;

      for (var i = 0; i < this.mvsimilar.length; i++) {
        if (i % 6 == 0) {
          j++;
          this.mvsimilarFormatted[j] = [];
          this.mvsimilarFormatted[j].push(this.mvsimilar[i]);
          this.similarShow = true;
        } else {
          this.mvsimilarFormatted[j].push(this.mvsimilar[i]);
          this.similarShow = true;
        }
      }
    });
  }
}
