import { Component, OnInit } from '@angular/core';
import { MoviesService } from '../../services/posts.service';

@Component({
  selector: 'app-carousel-continue',
  templateUrl: './carousel-continue.component.html',
  styleUrls: ['./carousel-continue.component.css'],
})
export class CarouselContinueComponent implements OnInit {
  public continue: any[] = [];
  public continueFormatted: any[] = [];
  public mobile: any;
  public show: any;

  constructor(private movieService: MoviesService) {}

  ngOnInit(): void {
    if (window.innerWidth < 360) {
      this.mobile = true;
    }
    console.log(window.innerWidth);
    for (let i = 0; i < localStorage.length; i++) {
      const key: any = localStorage.key(i);
      const value: any = localStorage.getItem(key);
      const media_id: any = value.slice(0, value.lastIndexOf('@'));
      const media_name: any = value
        .slice(0, value.lastIndexOf('/'))
        .slice(value.lastIndexOf('@') + 1);
      const media_poster: any = value
        .slice(0, value.lastIndexOf('$'))
        .slice(value.lastIndexOf('/') + 1);
      const media_type: any = value.slice(value.lastIndexOf('$') + 1);
      if (media_type == 'myMovie') {
        const myObj1: any = {
          id: media_id,
          name: media_name,
          poster: media_poster,
          type: 'movie',
        };
        this.continue.push(myObj1);
        this.show = true;
      } else if (media_type == 'myTv') {
        const myObj2: any = {
          id: media_id,
          name: media_name,
          poster: media_poster,
          type: 'tv',
        };
        this.continue.push(myObj2);
        this.show = true;
      }
    }
    var j = -1;
    for (var i = 0; i < this.continue.length; i++) {
      if (i % 6 == 0) {
        j++;
        this.continueFormatted[j] = [];
        this.continueFormatted[j].push(this.continue[i]);
      } else {
        this.continueFormatted[j].push(this.continue[i]);
      }
    }
  }
}
