import { Component, OnInit } from '@angular/core';
import { MoviesService } from '../../services/posts.service';

@Component({
  selector: 'app-mylist',
  templateUrl: './mylist.component.html',
  styleUrls: ['./mylist.component.css'],
})
export class MylistComponent implements OnInit {
  public watchlist_ids: any[] = [];
  public watchlist_names: any[] = [];
  public watchlist_posters: any[] = [];
  public watchlist_types: any[] = [];
  public empty = true;

  constructor(private movieService: MoviesService) {}

  ngOnInit(): void {
    for (let i = 0; i < localStorage.length; i++) {
      const key: any = localStorage.key(i);
      const value: any = localStorage.getItem(key);
      const media_id: any = value.slice(0, value.lastIndexOf('@'));
      const media_name: any = value
        .slice(0, value.lastIndexOf('/'))
        .slice(value.lastIndexOf('@') + 1);
      const media_poster: any = value
        .slice(0, value.lastIndexOf('$'))
        .slice(value.lastIndexOf('/') + 1);
      const media_type: any = value.slice(value.lastIndexOf('$') + 1);
      if (media_type == 'myListMovie') {
        this.watchlist_ids.unshift(media_id);
        this.watchlist_names.unshift(media_name);
        this.watchlist_posters.unshift(media_poster);
        this.watchlist_types.unshift('movie');
        this.empty = false;
      } else if (media_type == 'myListTv') {
        this.watchlist_ids.unshift(media_id);
        this.watchlist_names.unshift(media_name);
        this.watchlist_posters.unshift(media_poster);
        this.watchlist_types.unshift('tv');
        this.empty = false;
      }
    }
  }
}
