import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomepageComponent } from './components/homepage/homepage.component';
import { MvdetailsComponent } from './components/mvdetails/mvdetails.component';
import { MylistComponent } from './components/mylist/mylist.component';
import { TvdetailsComponent } from './components/tvdetails/tvdetails.component';

const routes: Routes = [
  { path: '', component: HomepageComponent },

  { path: 'mylist', component: MylistComponent },
  {
    path: 'watch',
    children: [
      {
        path: 'tv',
        children: [
          { path: '', component: HomepageComponent },
          { path: ':id', component: TvdetailsComponent },
        ],
      },
      {
        path: 'movie',
        children: [
          { path: '', component: HomepageComponent },
          { path: ':id', component: MvdetailsComponent },
        ],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
